import React, { useState } from "react";
import "./Header.css";
import { FiX, FiMenu } from "react-icons/fi";
import { Link } from "react-scroll";

const Header = () => {
  let [state, setstate] = useState(false);
  let handleclick = () => {
    setstate(!state);
  };

  return (
    <section class="header">
      <div className="container">
        <div className="header_logo">
          <h1 className="">Developer</h1>
        </div>
        <div className={state ? "header_nav_links d-flex active" : "header_nav_links d-flex"}>
        <Link activeClass="active"
            to="hero_section"
            smooth={true}
            duration={1000}
            className="anchor"
            onClick={handleclick}>
            Home
          </Link>
          <Link activeClass="active"
            to="about_me"
            smooth={true}
            duration={1000}
            className="anchor"
            onClick={handleclick}>
            About
          </Link>
          <Link activeClass="active"
            to="skills"
            smooth={true}
            duration={1000}
            className="anchor"
            onClick={handleclick}>
            Skills
          </Link>
          <Link activeClass="active"
            to="resume"
            smooth={true}
            duration={1000}
            className="anchor"
            onClick={handleclick}>
            Resume
          </Link>
          <Link activeClass="active"
            to="projects"
            smooth={true}
            duration={1000}
            className="anchor"
            onClick={handleclick}>
            projects
          </Link>
          
          <Link activeClass="active"
            to="contactForm"
            smooth={true}
            duration={1000}
            className="anchor"
            onClick={handleclick}>
            Contact
          </Link>
        </div>
        <div className="nav_icon d-md-none">
          <div className="icons" onClick={handleclick}>{state ? <FiX /> : <FiMenu />}</div>
        </div>
      </div>
    </section>
  );
};

export default Header;
