import React, { useEffect } from "react";
import "./skills.css";

import React_logo from "../../images/skills/react.png";
import HTML_logo from "../../images/skills/html.png";
import CSS_logo from "../../images/skills/css.png";
import Bootstrap_logo from "../../images/skills/bootstrap.png";
import JS_logo from "../../images/skills/js.png";
import Jquery_logo from "../../images/skills/jq.png";
import Php_logo from "../../images/skills/php.png";
import Node_logo from "../../images/skills/node_js.png";
import SQL_logo from "../../images/skills/sql.png";

import { ProgressBar} from "react-bootstrap";

//swiper
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, EffectCube } from "swiper";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/effect-cube";

// animation on scroll
import AOS from "aos";
import "aos/dist/aos.css";

const Skills = () => {
  useEffect(() => {
    AOS.init({});
  }, []);

  return (
    <section id="skills">
      <div className="container">
        <h2 className="heading">Professional Skills</h2>
        {/* <h3 className="sub_heading">My Skills</h3> */}

        <div className="silder_container">
        <Swiper
              autoplay={{
                delay: 0,
                disableOnInteraction: false,
              }}
              effect={"cube"}
              grabCursor={true}
              loop={true}
              speed={2000}
              cubeEffect={{
                shadow: true,
                slideShadows: true,
                shadowOffset: 20,
                shadowScale: 0.94,
              }}
              pagination={true}
              modules={[Autoplay, EffectCube]}
              className="mySwiper"
            >
              <SwiperSlide>
                <img src={HTML_logo} alt="Html Logo"/>
              </SwiperSlide>
              <SwiperSlide>
                <img src={CSS_logo} alt="Css Logo"/>
              </SwiperSlide>
              <SwiperSlide>
                <img src={JS_logo} alt="Javascript Logo"/>
              </SwiperSlide>
              <SwiperSlide>
                <img src={Bootstrap_logo} alt="Bootstrap Logo" />
              </SwiperSlide>
              <SwiperSlide>
                <img src={React_logo} alt="React Logo" />
              </SwiperSlide>
              <SwiperSlide>
                <img src={Jquery_logo} alt="Jquery Logo" />
              </SwiperSlide>
              <SwiperSlide>
                <img src={Php_logo} alt="Php Logo" />
              </SwiperSlide>
              <SwiperSlide>
                <img src={Node_logo} alt="Node Logo" />
              </SwiperSlide>
            </Swiper>
        </div>
        <div className="row">
          <div
            className="col-12 col-lg-6"
            data-aos="zoom-in-right"
            data-aos-duration="600"
          >
            
            <div className="skills_discription">
              <ul>
                <li>
                  Proficient in HTML, CSS, and JavaScript, with experience
                  creating responsive and mobile-friendly websites.
                </li>
                <li>
                  Skilled in creating dynamic and interactive user interfaces
                  using JavaScript frameworks such as React and jQuery.
                </li>
                <li>
                  Knowledgeable in web performance optimization techniques, such
                  as minification and lazy loading, resulting in faster load
                  times and improved user experience.
                </li>
                <li>
                  Familiar with integrating data from external sources using
                  APIs, including REST
                </li>
                <li>
                  Experienced in debugging front-end code using browser
                  developer tools, with proficiency in identifying and resolving
                  issues related to cross-browser compatibility and user
                  interface functionality.
                </li>
              </ul>
            </div>
          </div>

          <div
            className="col-12 col-lg-6"
            data-aos="zoom-in-left"
            data-aos-duration="600"
          >
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={HTML_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>HTML</label>
                <ProgressBar now={95}/>
                <span>95%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={CSS_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>Css</label>
                <ProgressBar now={92} />
                <span>92%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={JS_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>JavaScript</label>
                <ProgressBar now={80} />
                <span>80%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Bootstrap_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>Bootstrap</label>
                <ProgressBar now={90} />
                <span>90%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Jquery_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>Jquery</label>
                <ProgressBar now={85} />
                <span>85%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={React_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>React Js</label>
                <ProgressBar now={65} />
                <span>65%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Php_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>Php</label>
                <ProgressBar now={30} />
                <span>30%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={Node_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>Node Js</label>
                <ProgressBar now={25} />
                <span>25%</span>
              </div>
            </div>
            <div className="skills_content d-flex">
              <div className="logo">
                <img src={SQL_logo} alt="HTML" />
              </div>
              <div className="skills_progress_bar">
                <label>SQL</label>
                <ProgressBar now={60} />
                <span>60%</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
