import React, { useEffect, useState } from "react";
import "./AllProjects.css";
import Netflix from "../../images/projects/netflix.png";
import Calculator from "../../images/projects/calculator.png";
import Prr from "../../images/projects/prr-cabs.png";
import Portfolio from "../../images/projects/portfolio.png";
import Azhvar from "../../images/projects/azhvar.png";

import Web from '../../images/icons/web.png';



import AOS from "aos";
import "aos/dist/aos.css";

const AllProjects = () => {
  useEffect(() => {
    AOS.init({});
  }, []);

  let [showMore, setShowMore] = useState(false);
  const handleClick = () => {
    setShowMore(!showMore);
  };

  const btnStyle = {
    padding: '0px !important'
  }
  return (
    <section id="projects">
      <div className="container" data-aos="zoom-in-up" data-aos-duration="1000">
        <h2 className="heading mb-5">Projects</h2>
        
        <div className="project_content pb-4">
          <div className="row py-5 px-3">
            <div className="col-md-6">
              <div className="project_img text-center">
                <img src={Portfolio} alt="My Portfolio" />
                <button onClick={() =>window.location.reload()}><img src={Web} alt="Web View Logo"/>Web View</button> 
              </div>
            </div>
            <div className="col-md-6">
              <div className="project_details">
                <h3 className="sub_heading">My Portfolio</h3>
                <h4>Project Info:</h4>
                <p>
                  I created this portfolio using React JS to showcase my skills
                  and projects as a developer. The portfolio includes
                  information about my background, skills, experience, and
                  education. It also features a list ofsample projects that I
                  have worked on, along with a brief description of each and the
                  technology used to build them.
                </p>
                <h4>Technologies Used:</h4>
                <p>HTML, CSS, JavaScript, Bootstrap, React</p>
              </div>
            </div>
          </div>
        </div>
        <div className="project_content pb-4">
          <div className="row py-5 px-3">
            <div className="col-md-6">
              <div className="project_img text-center">
                <img src={Prr} alt="Prr Cabs Project"/>
                <button><img src={Web} alt="Web View Logo"/>
                  Web View
                </button>
              </div>
            </div>
            <div className="col-md-6">
              <div className="project_details">
                <h3 className="sub_heading">PRR Cabs</h3>
                <h4>Project Info:</h4>
                <p>
                  <p>
                    PRR Cabs Booking Service is a fully functional and
                    responsive website that I developed using HTML, CSS,
                    JavaScript, PHP, and MySQL. The website provides users with
                    an online platform to book cab services offered by PRR Cabs.
                  </p>
                  <div className="collapse" id="prr">
                    <p>
                      To enhance the user experience, I added interactive elements
                      such as a dynamic booking form that updates based on user
                      input. The vehicle details, such as available cab types,
                      vehicle images, and their respective prices, are dynamically
                      addable and editable from the admin panel. This was my first
                      experience developing an admin panel system, and I handled
                      the entire development process myself.
                    </p>
                    <p>
                      To ensure efficient management of booking information, I
                      implemented a back-end system that stores and retrieves user
                      data using PHP and MySQL. The complete front-end details,
                      such as available cab types, vehicle images, and their
                      respective prices, are managed from the backend.
                      Additionally, I utilized an API method to show the cab
                      details on the frontend.
                    </p>
                    <p>
                      This was my first full-stack website project, which
                      functioned like an e-commerce website. The project provided
                      me with a lot of knowledge about back-end development,
                      databases, API methods, and more. Overall, the project
                      allowed me to showcase my skills in web development,
                      database management, and project management.
                    </p>
                  </div>

                </p>
                <h4>Technologies Used:</h4>
                <p>HTML, CSS, JavaScript, Bootstrap, Jquery, Php, SQL</p>
              </div>
              <div className="btns readmore" style={btnStyle} >
                <button data-aos="fade-right" data-aos-duration="500" data-bs-toggle="collapse" data-bs-target="#prr" onClick={handleClick}>{showMore ? 'Read less' : 'Read more'}</button>
              </div>
            </div>
          </div>
        </div>
        <div className="project_content pb-4">
          <div className="row py-5 px-3">
            <div className="col-md-6">
              <div className="project_img text-center">
                <img src={Calculator} alt="Calculator" />
                <button onClick={()=> window.open('https://shabeer-01.github.io/Calculator_react_version/', '_blank')}><img src={Web} alt="Web View Logo"/>
                  Web View
                </button>
              </div>
            </div>
            <div className="col-md-6">
              <div className="project_details">
                <h3 className="sub_heading">Calculaor</h3>
                <h4>Project Info:</h4>
                <p>
                  <p>
                    Initially, I created a JavaScript Calculator project to
                    practice my event handling skills in JavaScript. Later, I
                    converted this project to a React app and implemented
                    keyboard event listeners and error handling to enhance the
                    user experience.
                  </p>
                  <div className="collapse" id="cal">
                    <p>
                      Through this project, I gained a deeper understanding of
                      state management and React components, and improved my
                      problem-solving skills by implementing complex mathematical
                      operations.
                    </p>
                  </div>
                </p>
                <h4>Technologies Used:</h4>
                <p>HTML, CSS, JavaScript, Bootstrap</p>
              </div>
              <div className="btns readmore" style={btnStyle} >
                <button data-aos="fade-right" data-aos-duration="500" data-bs-toggle="collapse" data-bs-target="#cal" onClick={handleClick}>{showMore ? 'Read less' : 'Read more'}</button>
              </div>
            </div>
          </div>
        </div>
        <div className="project_content pb-4">
          <div className="row py-5 px-3">
            <div className="col-md-6">
              <div className="project_img text-center">
                <img src={Azhvar} alt="Azhvar Project" />
                <button onClick={() => window.open('https://azhvar.in/', '_blank')}> <img src={Web} alt="Web View Logo"/>
                  Web View
                </button>
              </div>
            </div>
            <div className="col-md-6">
              <div className="project_details">
                <h3 className="sub_heading">Accounting Service</h3>
                <h4>Project Info:</h4>
                <p>
                  The Azhvar Consultancy Accounting and Tax Paying Service website is a prime example of my
                  proficiency in web development, specifically in HTML and CSS with mobile responsive design.
                </p>
                <div className="collapse" id="azhvar">
                  <p>  The website provides comprehensive information about the accounting and
                    tax services offered by vp associates, and features a professional and modern static design.
                  </p>
                  <p>To ensure optimal viewing and functionality across different devices, I created a responsive layout using HTML and CSS. Additionally, I incorporated high-quality images and a user-friendly navigation menu to improve the overall user experience.</p>
                  <p>Overall, this project allowed me to showcase my expertise in HTML, CSS, responsive web design, and user experience design. It was a great opportunity for me to demonstrate my ability to deliver high-quality work that meets the needs of clients and exceeds their expectations.</p>
                </div>
                <h4>Technologies Used:</h4>
                <p>HTML, CSS, JavaScript, Bootstrap</p>
              </div>
              <div className="btns readmore" style={btnStyle} >
                <button data-aos="fade-right" data-aos-duration="500" data-bs-toggle="collapse" data-bs-target="#azhvar" onClick={handleClick}>{showMore ? 'Read less' : 'Read more'}</button>
              </div>
            </div>
          </div>
        </div>
        <div className="project_content pb-4">
          <div className="row py-5 px-3">
            <div className="col-md-6">
              <div className="project_img text-center">
                <img src={Netflix} alt="Netflix Login Page"/>
                <button onClick={()=> window.open('https://shabeer-01.github.io/netflix-login-page/', '_blank')}> <img src={Web} alt="Web View Logo"/>
                  Web View
                </button>
              </div>
            </div>
            <div className="col-md-6">
              <div className="project_details">
                <h3 className="sub_heading">Netflix Login Page</h3>
                <h4>Project Info:</h4>
                <p>
                  I Have apply my HTML and CSS skills to create a responsive web
                  page that closely resembles the original Netflix login page. I
                  also took on the challenge of converting this static page into
                  a fully-functional React app, implemented form validation
                  using react hook concept.
                </p>
                <h4>Technologies Used:</h4>
                <p>HTML, CSS, JavaScript, Bootstrap, React</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default AllProjects;
