import React, {useEffect} from "react";
import "./HeroSection.css";

import AOS from 'aos';
import 'aos/dist/aos.css';

const HeroSection = () => {

  useEffect(() => {
    AOS.init({
      duration: 900, // animation duration in milliseconds
    });
  }, []);

  return (
    <section id="hero_section">
      <div className="container">
        <div className="personal_details">
          <h1 className="name" data-aos="fade-down">Shabeer Basha Babu</h1>
          <div className="work" data-aos="fade-left">Front End Developer</div>
          <div className="btns">
              <button data-aos="fade-right" data-aos-duration="1000">
                <a
                  href="../../images/personal/resume.pdf"
                  download
                  className="download_btn"
                >
                  Download CV
                </a>
              </button>
              <button
                data-aos="fade-left"
                data-aos-duration="1500"
                data-aos-offset="100"
              >
                <a href="#Projects">Projects</a>
              </button>
            </div>
        </div>
        <div className="bg_img">
          {/* <img src={Bg} /> */}
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
