import React from "react";
import "./ContactForm.css";
import Phone from '../../images/icons/phone.png';
import Email from '../../images/icons/email.png';
import Github from '../../images/icons/github.png';
import LinkedIn from '../../images/icons/linkedin.png';

const ContactForm = () => {
  return (
    <section id="contactForm">
      <div className="container">
        <h2 className="title mb-5">Contact</h2>
        <div className="row">
          <div className="col-lg-6 p-4">
            <div className="all_contact_details d-flex justify-content-center">
              <div className="contact_links d-flex p-3 mb-3">
                <img src={Phone} alt="phone" />
                <div className="">
                  <label className="">Phone Number</label>
                  <a href="#" className="phone ps-3"> +91 9047599627</a>
                </div>
              </div>
              <div className="contact_links d-flex p-3 mb-3">
                <img src={Email} alt="email"/>
                <div className="">
                  <label className="">Email Address</label>
                  <a href="#" className="phone ps-3">shabeerbashababu@gmail.com</a>
                </div>
              </div>
              <div className="contact_links d-flex p-3 mb-3">
                <img src={Email} alt="email"/>
                <div className="">
                  <label className="">Email Address</label>
                  <a href="#" className="phone ps-3">shabeerbashababu@gmail.com</a>
                </div>
              </div>
              
            </div>
          </div>
          <div className="col-lg-6">
            <form className="pt-3">
              <div className="row">
                <div className="col-lg-6">
                  <label>Name</label>
                  <input type="text" placeholder="Enter Your Name" />
                </div>
                <div className="col-lg-6">
                  <label>Email</label>
                  <input type="email" placeholder="Enter Your Email" />
                </div>
                <div className="col-lg-6">
                  <label>Phone Number</label>
                  <input type="number" placeholder="Enter Your phone Number" />
                </div>
                <div className="col-lg-6">
                  <label>Subject</label>
                  <input type="text" placeholder="Enter Subject" />
                </div>
                <div className="col-12 textarea">
                  <label>Message</label>
                  <textarea placeholder="Enter Your Message" rows={5} cols={5} />
                </div>
                <div className="col-12 submitButtom">
                  <button>Submit</button>
                </div>
              </div>
            </form>
          </div>
        </div>

      </div>
    </section>
  );
};

export default ContactForm;
