import React, { useEffect } from "react";
import "./Resume.css";

import AOS from "aos";
import "aos/dist/aos.css";
const Resume = () => {
    useEffect(() => {
      AOS.init({
      });
    }, []);
  return (
    <section id="resume">
      <div className="container">
        <div className="row">
          <h2 className="heading">Resume</h2>
          <div className="col-12 col-lg-6" data-aos="zoom-in-right" data-aos-duration="600">
            <h2 className="heading">Education</h2>
            <div className="education_details mb-4">
              <h3 className="course">Bachelor Of Statistics</h3>
              <div className="d-flex justify-content-between">
                <div className="edu_institusion">St.Joseph's College, Trichy</div>
                <div className="year_of_passing">2017 - 2020</div>
                <div className="marks">60%</div>
              </div>
            </div>
            <div className="education_details mb-4">
              <h3 className="course">Higher Secondary</h3>
              <div className="d-flex justify-content-between">
                <div className="edu_institusion">St.Joseph's College, Trichy</div>
                <div className="year_of_passing">2015 - 2017</div>
                <div className="marks">78.5%</div>
              </div>
            </div>
            <div className="education_details mb-4">
              <h3 className="course">SSLC</h3>
              <div className="d-flex">
                <div className="edu_institusion">Dhanalakshmi Srinivasan matric hr sec school</div>
                <div className="year_of_passing">2013 - 2015</div>
                <div className="marks">96.8%</div>
              </div>
            </div>
          </div>
          <div className="col-12 col-lg-6" data-aos="zoom-in-left" data-aos-duration="600">
            <h2 className="heading">My Experience</h2>
            <div className="experiance_details">
              <h3 className="company_name">Cryptographic Solutions</h3>
              <div className="job_type">
                Front End Developer (Aug-2022 - Present)
              </div>
              <div className="job_discription">
                    As a front-end developer I used to design and develop a
                    complete front-end responsive websites with basics of SEO
                    services for boosting the website. I have worked 
                    in several projects individually and also as a team.
              </div>
            </div>
            <div className="experiance_details">
              <h3 className="company_name">Vijay Soft Solutions Pvt Ltd</h3>
              <div className="job_type">
                Technical Support (Jan-2022 - Jul-2022)
              </div>
              <div className="job_discription">
                Worked as a technical support. I used to visit the client
                location to provide support. Also I provide online phone
                call support
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Resume;
