import React, { useEffect, useState } from "react";
import "./AboutMe.css";
import Img from "../../images/personal/per_img.jpeg";

import AOS from "aos";
import "aos/dist/aos.css";

const About = () => {
  useEffect(() => {
    AOS.init({});
  }, []);

  
  let [showMore, setShowMore] = useState(false);
  const handleClick = () => {
    setShowMore(!showMore);
  };


  return (
    <section id="about_me">
      <div className="container about_section">
        <h2 className="heading pb-3">About</h2>

        <div className="row">
          <div
            className="col-12 col-lg-5"
            data-aos="zoom-in-right"
            data-aos-duration="600"
          >
            <div className="per_img">
              <img src={Img} />
            </div>
          </div>
          <div
            className="col-12 col-lg-7"
            data-aos="zoom-in-left"
            data-aos-duration="600"
          >
            <h3 className="sub_heading pb-3">About Me</h3>
            <div className="discription" >
              <p>
                I am a skilled and professional Front-End developer with
                experience in Front-End development and design. Over a short
                period of time, I have worked on several projects and gained
                extensive experience in front-end and full-stack web
                development, including creating engaging and responsive user
                interfaces and building complete admin panels
              </p>
              <div className="collapse" id="demo">
                <p>
                  In addition to my expertise in front-end technologies like HTML,
                  CSS, and JavaScript, I also have knowledge in back-end
                  technologies like PHP, Node, and database management. I am a
                  quick learner with a passion for keeping up with the latest
                  front-end development technologies and trends.
                </p>
                <p>
                  I pride myself on my ability to adapt to new frameworks and
                  tools quickly and efficiently. I am equally comfortable working
                  independently or as part of a team, and I value collaboration
                  and communication in order to deliver high-quality results on
                  time and on budget.
                </p>
                <p>
                  I am excited about the opportunity to take on new challenges and
                  contribute my skills to the success of any team or project.
                </p>
              </div>
            </div>
            <div className="btns">
              <button data-aos="fade-right" data-aos-duration="500" data-bs-toggle="collapse" data-bs-target="#demo" onClick={handleClick}>{showMore ? 'Read less' : 'Read more'}</button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
